import { useMutation, useQuery, useQueryClient } from "@tanstack/react-query";
import { CreateLeadDTO, Lead } from "@everlast/types";
import { createLead, listLeads } from "@/lib/mock-api";

const DEFAULT_TENANT = "tenant-demo-1";

export function useLeads(tenantId: string = DEFAULT_TENANT) {
  return useQuery({
    queryKey: ["leads", tenantId],
    queryFn: () => listLeads(tenantId),
  });
}

export function useCreateLead(tenantId: string = DEFAULT_TENANT) {
  const client = useQueryClient();
  return useMutation({
    mutationFn: (payload: Omit<CreateLeadDTO, "tenantId">) =>
      createLead({ tenantId, ...payload }),
    onSuccess: (lead: Lead) => {
      client.invalidateQueries({ queryKey: ["leads", tenantId] });
      client.invalidateQueries({ queryKey: ["lead", tenantId, lead.id] });
    },
  });
}

