import Link from "next/link";
import { Lead } from "@everlast/types";
import { Card } from "@/components/ui/card";
import { StatusBadge } from "@/components/status-badge";
import { formatDistanceToNow } from "date-fns";
import { de } from "date-fns/locale";

type LeadsTableProps = {
  leads: Lead[];
};

export function LeadsTable({ leads }: LeadsTableProps) {
  if (!leads.length) {
    return (
      <Card className="flex h-40 items-center justify-center text-sm text-muted-foreground">
        Noch keine Leads vorhanden.
      </Card>
    );
  }

  return (
    <Card className="divide-y">
      <div className="grid grid-cols-5 gap-4 px-4 py-3 text-xs font-medium uppercase tracking-wide text-muted-foreground">
        <span>Lead</span>
        <span>Status</span>
        <span>Email</span>
        <span>Telefon</span>
        <span>Erstellt</span>
      </div>
      {leads.map((lead) => (
        <div
          key={lead.id}
          className="grid grid-cols-5 gap-4 px-4 py-4 text-sm hover:bg-muted/50"
        >
          <Link
            href={`/leads/${lead.id}`}
            className="font-medium text-primary underline-offset-2 hover:underline"
          >
            {lead.name}
          </Link>
          <StatusBadge variant="lead" status={lead.status} />
          <span>{lead.email ?? "—"}</span>
          <span>{lead.phone ?? "—"}</span>
          <span className="text-muted-foreground">
            {formatDistanceToNow(new Date(lead.createdAt), {
              locale: de,
              addSuffix: true,
            })}
          </span>
        </div>
      ))}
    </Card>
  );
}

